.menu-bar {
  background-color: black;
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  position: relative;
 
}

.menu-bar ul {
  list-style: none;
  display: flex;
}

.menu-bar ul li {
  /* width: 120px; */
  padding: 10px 30px;
  /* text-align: center; */
  position: relative;
}
.search{
  width:-webkit-max-content;
  width:max-content;
  display: flex;
  align-items:center ;
  padding: 14px;
  margin-right: 140px;
  border-radius: 28px;
  background: #f6f6f6;
  height: 40px;
  transition: box-shadow 0.25s ;
}
.search:focus-within{
  box-shadow:0 0 10px white ;
}



.searchinput{
  font-size: 16px;
  margin-left:14px ;
  color: #333333;
  font-family:'Lexend',sans-serif;
  border: none;
  outline: none;
  background: transparent;
  width: 250px;
}
.searchinput::placeholder,.searchicon{
  color: rgb(143, 131, 131);
}
.menu-bar ul li a {
  font-size: 20px;
    color: var(--color-white);
    color: white !important;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.3s;
}

.menu-bar ul li a:hover {
  color: rgb(144, 149, 221);
}

.dropdown-menu {
  display: none;
}

.menu-bar ul li:hover .dropdown-menu {
  display: block;
  position: absolute;
  left: 0;
  top: 100%;
  background-color: #a48787;
}

.menu-bar ul li:hover .dropdown-menu ul {
  display: block;
  margin: 10px;
}

.menu-bar ul li:hover .dropdown-menu ul li {
  width: 150px;
  padding: 10px;
}
@keyframes slidy {
  0% {
      left: 0%;
  }
  20% {
      left: 0%;
  }
  25% {
      left: -100%;
  }
  45% {
      left: -100%;
  }
  50% {
      left: -200%;
  }
  70% {
      left: -200%;
  }
  75% {
      left: -300%;
  }
  95% {
      left: -300%;
  }
  100% {
      left: -400%;
  }
}

