/* product section */

.products {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  padding: 15px;
  margin-left:0px ;
  margin-bottom: 50px;
  
  
}
.boxed{
  width: 200px;
}
.products .text-light {
  text-align: center;
  width: 70%;
  margin: 0.9rem auto;
}


.boxed:hover {
  transform: scale(1.1);

}

.product-content {
  background: var(--gray);
  padding: 3rem 0.5rem 2rem 0.5rem;
  cursor: pointer;
}

.product-img {
  background: var(--white-light);
  box-shadow: 0 0 20px 10px var(--white-light);
  width: 200px !important;
  height: 270px !important;
  margin: 0 auto;
  border-radius: 10px;
  transition: background 0.5s ease;
}
.product-img:hover{
  box-shadow: 3px 3px 3px 3px rgb(219, 90, 90);
}
.product-btns {
  margin-top:15px ;
  justify-content: center;
  transition: opacity 0.6s ease;
}

.btn-buy {
  background: #776d6d;
  border: 1px solid black;
  padding: 0.8rem 0;
  width: 125px;
  font-family: inherit;
  text-transform: uppercase;
  cursor: pointer;
  color: black;
  border: none;
  margin-right: 50px;
  transition: all 0.6s ease;
}



.btn-buy:hover {
  background: #050505;
  color: rgb(230, 226, 226);
}

.product-info {
  margin-top:20px ;
  background: white;
  padding: 0rem 2rem 0rem 2rem;
}

.product-info-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rating{
  color: var(--carribean-green);
  padding-top: 2rem;
  padding-left: -3rem;
  padding-right: 0.3rem;
}

.product-name {
  color: black;
  display: block;
  text-decoration: none;
  font-size: 0.8rem;
  height: 30px;
  text-transform: uppercase;
  font-weight: bold;
}

.product-price {
  padding-top: 2rem;
  padding-right: 0.3rem;
  display: inline-block;
}
.product-price1{
  padding-right: 0.3rem;
  display: inline-block;
}

.product-price:first-of-type {
  text-decoration: line-through;
  color: var(--carribean-green);
}

.product-img img {
  transition: transform 0.6s ease;
  height: 250px;
  width: 200px;
}


.product:hover .product-img {
  background: var(--carribean-green);
}

.product:hover .product-btns {
  opacity: 1;
}

.off-info .sm-title {
  background: var(--carribean-green);
  color: white;
  display: inline-block;
  padding: 0.5rem;
  position: absolute;
  top: 0;
  left: 0;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  z-index: 1;
  letter-spacing: 3px;
  cursor: pointer;
}


/* product collection */

.product-collection {
  padding: 3.2rem 0;
}

.product-collection-wrapper {
  padding: 3.2rem 0;
}

.product-col-left {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url("https://www.nicobar.com/cdn/shop/files/NBI027339_1.jpg?v=1685432020") center/cover no-repeat;
}

.product-col-r-top {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url("https://uptownie.com/cdn/shop/files/IMG_5433_1_900x.jpg?v=1683796580") center/cover no-repeat;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 50vh;
  padding: 2rem 1.5rem 3.2rem;
  margin: 5px;
}

.product-col-r-bottom>div:first-child {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url("https://imageio.forbes.com/specials-images/imageserve/62f41a29eec6c603aa750efd/Feature-Image-Summer-Clothing/960x0.jpg?format=jpg&width=960") center/cover no-repeat;
}

.product-col-r-bottom>div:last-child {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url("https://boutique-magazine.co.uk/wp-content/uploads/2022/06/INDX-Womenswear-Feb-22-32.jpg") center/cover no-repeat;
}

.product-col-content {
  text-align: center;
  color: white;
}

.product-collection .text-light {
  opacity: 1;
  font-size: 0.8;
  font-weight: 400;
  line-height: 1.7;
}

.btn-dark {
  background: black;
  color: white;
  outline: 0;
  border-radius: 25px;
  padding: 0.7rem 1rem;
  border: 0;
  margin-top: 1rem;
  cursor: pointer;
  transition: all 0.6s ease;
  font-size: 1rem;
  font-family: inherit;
}

.btn-dark:hover {
  background: var(--carribean-green);
}


/* Media Queries */

@media screen and (min-width: 992px) {
  .product-items {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
  }
  .product-col-r-bottom {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .product-items {
      grid-template-columns: repeat(3, 1fr);
  }
  .product {
      margin-right: 1rem;
      margin-left: 1rem;
  }
  .products .text-light {
      width: 50%;
  }
}

@media screen and (min-width: 1336px) {
  .product-items {
      grid-template-columns: repeat(4, 1fr);
  }
  .product-collection-wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
  }
  .flex {
      height: 60vh;
  }
  .product-col-left {
      height: 121.5vh;
  }
}

