.side-navigation h5 {
    font-size: 16px;
    font-weight: bold;
}

.profile-breadcrumb a,
.side-navigation a {
    color: black;
    text-decoration: none;
}

.my-account,
.orders,
.account,
.wishlist-payment {
    padding: 20px;
    line-height: 30px;
    font-size: 14px;
}

.orders a:hover,
.account a:hover,
.wishlist-payment a:hover {
    color: #6c757d;
    font-weight: bold;
}

.account .active {
    background-color: #e9ecef;
    color: #6c757d;
    font-weight: bold;
    padding: 0 10px !important;
}

.account .nav-link {
    padding: 0;
}

.profile-breadcrumb .active {
    font-weight: bold;
}

.row {
    margin-right: unset;
    margin-left: unset;
}

/* __________end of side navigation bar__________ */

.wishlist-body-top {
    margin-top: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #d9d9d9;
}

.wishlist-body-top div:first-child h5 {
    margin: 0;
}

.wishlist-body-top-heading {
    grid-column-gap: 20px;
    column-gap: 20px;
}

.wishlist-body-top-heading div:nth-child(2) i {
    color:#6c757d;
}

.sort-by select {
    border: 1px solid #000000;
    font-weight: bold;
}

.wishlist-body-top-heading div:first-child span:first-child {
    font-size: 25px;
    font-weight: bold;
}

.wishlist-body-top-heading div:first-child span:nth-child(2) {
    font-size: 16px;
    font-weight: bold;
}

.wishlist-body-top-heading div:first-child {
    grid-column-gap: 5px;
    column-gap: 5px;
}

.wishlist-body-top-heading div:nth-child(2) {
    grid-column-gap: 5px;
    column-gap: 5px;
    font-size: 16px;
}

.sort-by {
    font-size: 14px;
}

.wishlist-item {
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    display: flex;
    margin-bottom: 20px;
}

.item-img {
    padding: 20px;
    display: inline-block;
}

.item-img img {
    width: 180px;
    height: 200px;
    border: 1px solid #d9d9d9;
    background-color: #f5f5f5;
}

.item-detail {
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.item-detail div:first-child h6 {
    margin: 0;
    font-size: 16px;
    font-weight: bold;
}

.item-rating ul {
    list-style: none;
    display: flex;
    align-items: center;
    padding: 0;
    grid-column-gap: 5px;
    column-gap: 5px;
    font-size: 18px;
    color: #ffcc33;
    margin: 0;
}

.select-size select {
    width: 120px;
    padding: 5px;
    border-radius: 5em;
    background-color: #cccccc;
    border: 0px;
    font-size: 14px;
}

.select-size select option {
    font-size: 14px;
}

.addcart-btn {
    border-radius: 10px;
    border: 2px solid#6c757d;
    background-color: #6c757d;
    color: #ffff;
    font-weight: bold;
    height: 40px;
    width: 150px;
    border-radius: 2em;
}

.addcart-btn:hover {
    color:#6c757d;
    background-color: #ffff;
    border-radius: 2em;
}

.addcart-removewishlist {
    grid-column-gap: 20px;
    column-gap: 20px;
    display: flex;
    justify-content: left;
    align-items: center;
    margin-top: 20px;
    font-size: 16px;

}

.addcart-removewishlist span:nth-child(3):hover {
    color: #6c757d;
    font-weight: bold;
    cursor: pointer;
}

.price-discount {
    font-size: 16px;
}

.price-discount span:first-child {
    font-weight: bold;
}

.price-discount span:nth-child(2) {
    text-decoration: line-through;
}

.price-discount span:nth-child(3) {
    font-weight: bold;
    color: #6c757d;
}

@media screen and (min-width: 200px) and (max-width: 860px) {

    .my-account,
    .orders,
    .account,
    .wishlist-payment {
        padding: 10px;
        line-height: 25px;
        font-size: 12px;
    }

    .wishlist-body-top-heading {
        grid-column-gap: 10px;
        column-gap: 10px;
    }

    .side-navigation h5,
    .wishlist-body-top-heading div:first-child span:nth-child(2),
    .wishlist-body-top-heading div:nth-child(2),
    .item-detail div:first-child h6,
    .price-discount,
    .addcart-removewishlist {
        font-size: 14px;
    }

    .sort-by {
        font-size: 12px;
    }

    .select-size select {
        width: 100px;
    }

    .item-img,
    .item-detail {
        padding: 10px;
    }

    .addcart-removewishlist {
        grid-column-gap: 10px;
        column-gap: 10px;
        margin-top: 10px;
    }

    .addcart-btn {
        height: 35px;
        width: 100px;
    }

    .item-img img {
        width: 150px;
        height: 170px;
    }

    .item-rating ul {
        grid-column-gap: 2px;
        column-gap: 2px;
        font-size: 16px;
    }

    @media screen and (min-width: 200px) and (max-width: 600px) {
        .profile-breadcrumb ol {
            padding: 10px 0px;
            font-size: 12px;
        }

        .padding-none {
            padding: 0;
        }

        .my-account,
        .orders,
        .account,
        .wishlist-payment {
            padding: 10px 0;
        }

        .wishlist-body-top-heading div:first-child span:first-child {
            font-size: 16px;
        }

        .wishlist-item {
            margin-bottom: 10px;
        }

        .side-navigation {
            display: grid !important;
            grid-template-columns: auto auto !important;
            justify-content: space-around !important;
        }

        .wishlist-body-top {
            display: grid !important;
            grid-template-columns: auto;
            line-height: 30px;
        }

        .wishlist-item {
            display: grid !important;
            grid-template-columns: auto;
        }

        .addcart-removewishlist {
            justify-content: start;
        }

        .item-img img {
            width: 130px;
            height: 150px;
        }

        .my-account img {
            width: 50px;
        }

        .item-detail {
            line-height: 30px;
        }

        .item-img,
        .item-detail {
            padding: 10px 0;
        }

        .addcart-removewishlist {
            font-size: 12px;
        }

        .wishlist-body-top-heading div:first-child span:nth-child(2),
        .wishlist-body-top-heading div:nth-child(2) {
            font-size: 12px;
        }

        .wishlist-body-top-heading div:first-child {
            grid-column-gap: 0px;
            column-gap: 0px;
        }
    }
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
}

.totcontainer {
    width: 80%;
    margin: auto;
    padding: 2rem;
    background-color: white;
    display: flex;
    justify-content: spacebetween;
    flex-direction: row;
}
.totcontainer>div{
margin: 2rem;
}

.headingflex{
    display: flex;
    justify-content: space-between;
}
.headingflex>h1 {
    text-align: left;
    font-size: 15px;
    margin-bottom: 2rem;
}
.headingflex>button{ 
  font-size: 12px;
  font-weight: bold;
  height: 40px;
  padding:0px 10px;
  border-radius: 5px;
  cursor: pointer;
  background-color: #007bff;
    color: white;
    border: none;
  transition: background-color 1s ease;
     
}
.headingflex>button:hover{
    background-color: #0c335c;
    color: white;
    border: none;
}

.addressdetails {
    margin-bottom: 1rem;
    padding: 1rem;
    font-size: 12px;
    width: 450px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.addressdetails h4 {
    font-size:12px;
    font-weight: bold;
    margin-bottom: 0.5rem;
}


.addressdetails p {
    opacity: 0.9;
    margin-bottom: 0.2rem;
}

.add-new-address {
    display: block;
    width: 100%;
    padding: 0.5rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 1s ease;
     
}
.delete-address {
    display: block;
    width: 30%;
    padding: 0.5rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 1s ease;
     
}
.delete-address:hover,.add-new-address:hover{
    background-color: #0c335c;
    color: white;
    border: none;
}
.delivery-estimates,
.price-details {
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 0.5rem;
}

.delivery-estimates h4,
.price-details h4 {
    font-size:12px;
    font-weight: bold;
    margin-bottom: 0.5rem;
}
.delivery-estimates p,
.price-details p {
    font-size:12px;
    opacity: 0.9;
    margin-bottom: 0.5rem;
}
.popup{
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 700px;
    position: absolute;
    top: 0;
    display: none;
    justify-content: center;
    align-items: center;

}
.popup-content{
    height: 530px;
    width: 500px;
    background: white;
    padding: 20px;
    border-radius: 5px;
    position: relative;
}
.close{
    position: absolute;
    top: -15px;
    right: -15px;
    background-color: white;
    height: 20px;
    width: 20px;
    cursor: pointer;
    border-radius: 50%;
}
.addressinput{
    width: 70%;
    height: 40px;
    padding:5px;
    margin-bottom: 10px;
    border-radius: 5px;
    font-size: 12px;
    border: 1px solid rgb(141, 135, 135);
}
